<template>
    <div class="device-revocation-row">
        <div class="device-revocation-detail">
            <mt-cell label="" placeholder="" :value="accountInfo.USER_NAME"></mt-cell>
            <mt-cell label="" placeholder="" :value="accountInfo.IDCARD"></mt-cell>
            <mt-cell label="" placeholder="" :value="accountInfo.MOBILE"></mt-cell>
            <mt-cell class="mint-select" title="">
                <select name="" id="" class="mint-field-core" v-model="accountInfo.ACCT_TYPE">
                    <option v-for="(item, index) in accountTypeData" :key="index" :value="item.value">
                        {{item.label}}
                    </option>
                </select>
            </mt-cell>
        </div>
        <span style="display: block;padding: 10px 0 0 10px;color: #329eef;">:</span>
        <div class="device-revocation-detail detail-items-li">
            <mt-cell class="mint-select" title="">
                <select name="" id="" class="mint-field-core" v-model="accountInfo.ACCTSOURCE">
                    <option v-for="(item, index) in actionData" :key="index" :value="item.value">
                        {{item.label}}
                    </option>
                </select>
            </mt-cell>
            <mt-field label="" placeholder="" v-model="accountInfo.PAYER_ACCOUNT"></mt-field>
            <mt-cell class="mint-select" title="">
                <select name="" id="" class="mint-field-core" v-model="accountInfo.BINDACCTTYPE">
                    <option v-for="(item, index) in typeData" :key="index" :value="item.value">
                        {{item.label}}
                    </option>
                </select>
            </mt-cell>
        </div>
        <div style="height:50px;line-height:50px">
            <input type="checkbox" v-model="accountInfo.checkbox" style="width: 20px;height: 20px;margin-top: -2px;margin-bottom: 1px;vertical-align: middle;">
            <span style="color:red"><a href="#doc"></a></span>
        </div>
        <div class="device-revocation-detail">
            <div class="detail-items">
                <span style="display:block"><b></b></span>
                <span>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;1205000101000</span>
            </div>
        </div>
        <div class="field-action inline">
            <mt-button @click="back()"></mt-button>
            <mt-button @click="next()" :disabled="!submitStatus" size="large"></mt-button>
        </div>
    </div>
</template>

<script>


import * as service from '@/services/my/account-opening';
// import { mapGetters, mapActions, mapState } from 'vuex';

export default {
    data() {
        return {
            accountTypeData: [
                {
                    value: '',
                    label: ''
                },
                {
                    value: 'DEPS1002',
                    label: ''
                },
                {
                    value: 'DEPS1003',
                    label: ''
                }
            ],
            actionData: [
                {
                    value: '',
                    label: ''
                },
                {
                    value: '0',
                    label: ''
                },
                {
                    value: '1',
                    label: ''
                }
            ],
            typeData: [
                {
                    value: '',
                    label: ''
                },
                {
                    value: '2',
                    label: ''
                },
                {
                    value: '3',
                    label: ''
                }
            ],
            submitStatus: false,
            accountInfo: {
                USER_NAME: '', // 
                IDCARD: '', // 
                MOBILE: '', // 
                ACCT_TYPE: '', // 
                PAYER_ACCOUNT: '', // 
                ACCTSOURCE: '', // 
                BINDACCTTYPE: '', // 
                PAY_PWD: '', // 
                checkbox: ''
            }
        };
    },
    created() {
        this.accountInfo = Object.assign({}, this.$store.state.accountOpening.search);
        var id = this.$route.query.id || '';
        service.getAccountOpeningIndex().then((res) => {
            this.accountInfo.USER_NAME = res.USER_NAME;
            this.accountInfo.IDCARD = res.IDCARD;
            this.accountInfo.MOBILE = res.MOBILE;
        }, (err) => {
            this.$alert(err.MESSAGE, {
                btn: { text: '' }
            });
        });
    },
    computed: {
    },
    components: {
    },
    watch: {
        accountInfo: {
            handler(item){
                if(item.PAYER_ACCOUNT && item.ACCT_TYPE && item.ACCTSOURCE && item.BINDACCTTYPE){
                    this.submitStatus = true;
                }else{
                    this.submitStatus = false;
                }
            },
            deep: true
        }
    },
    methods: {
        back() {
            this.$router.go(-1);
        },
        next() {
            let _data = Object.assign({}, this.accountInfo);
            if (this.$validate.isEmpty(_data.ACCT_TYPE)) {
                this.doMsg('');
                return false;
            }
            if (this.$validate.isEmpty(_data.ACCTSOURCE)) {
                this.doMsg('');
                return false;
            }
            if (this.$validate.isEmpty(_data.PAYER_ACCOUNT)) {
                this.doMsg('');
                return false;
            }
            if (this.$validate.isEmpty(_data.BINDACCTTYPE)) {
                this.doMsg('');
                return false;
            }
            if (!_data.checkbox) {
                this.doMsg('');
                return false;
            }
            this.$router.push('/account-opening/pay');
        },
        doMsg(msg) {
            this.$toast(msg);
        }
    },
    beforeRouteLeave (to, from, next) {
        if( to.meta['groupName'] === from.meta['groupName']){
            this.$store.dispatch('updateAccountOpeningSearch', this.accountInfo );
        }else{
            this.$store.dispatch('clearAccountOpeningSearch');
        }
        next( true );
    }
};
</script>


<style lang="scss">
.mint-cell.mint-select .mint-cell-title {
    width: 250px;
}
.device-revocation-row {
    .device-revocation-detail {
        background: #fff;
        border: 1px solid #ddd;
        border-radius: 20px;
          .detail-items {
            padding: 20px;
            border-bottom: 1px solid #ddd;
                label {
                    display: inline-block;
                    width: 40%;
                    height: 60px;
                    line-height: 60px;
                }
          }
    }
}
.detail-items-li {
    margin-top: 20px;
}

</style>



